import { NavLink } from 'react-router-dom'
import { ReactComponent as Brand } from './companylogo.svg'
import './navbar.css'
import React from 'react';

const Navbar = ({ openPanel }) => {
  return (
    <nav className="navbar">
      <div className="container">
        <div className="logo">
          <Brand />
        </div>
        <div className="CompanyName">
            <p>RIP CURRENT INTERACTIVE LLC</p>
        </div>
        <div className="nav-elements">
          <ul>
            
          

            <button className="buttons" onClick={() => openPanel({
              title: '',
              text: 'Not FOR REAL MONEY SLOT MACHINES',
              image: 'Casino Games.jpg',
              buttons: [
                { label: 'Ghastly Riches', onClick: () => console.log('Button 1 clicked') },
                { label: 'Currents Of Fortune', onClick: () => console.log('Button 2 clicked') },
              ],
            })}>Projects</button>
            <button className="buttons" onClick={() => openPanel({
              title: 'About',
              text: 'About Content',
              image: 'About.jpg',
            
            })}>About</button>
           <button className="buttons" onClick={() => openPanel({
              title: 'Contact',
              text: 'Email: RipCurrentInteractive@gmail.com',
              image: 'Contact.jpg',
            
            })}>Contact</button>
          </ul>
        </div>
      </div>
    </nav>
  )
}

export default Navbar